import house from "./imgs/logo.png";
import logo from "./imgs/house.png";
import  Home from "./home/Home";
import React from "react";
import reactDom from "react-dom";

function Header() {


    return (
      <div className="Header" style={{width:"100%" ,marginTop:"0px", paddingTop:"0px", marginBottom:"10px"}}>
            <div id="header" style={{width: "100%", height:"auto", textAlign: "center", backgroundColor: "lightgreen", borderBottom:"2px solid green"}}> 
                <img onClick={renderHome} src={logo} style={{width: "60%" , maxHeight: "auto", marginLeft: "auto", marginRight: "auto"}}></img>
            </div>

            <div id="logo-logo" onClick={renderHome} style={{width: "150px", height: "auto", marginLeft: "auto", marginRight: "auto"}}>
                  <img src={house} width="100%" height="auto" ></img>
            </div>

     </div>
    );
  }

  function renderHome(){
    reactDom.render(
      <React.StrictMode>
    <Header />
    <Home />
  </React.StrictMode>,document.querySelector("#root")
    )
  }
  
  export default Header;
  